.profileCard {
  display: flex;
  flex-direction: column;
  border-radius: 1.5rem;
  position: relative;
  gap: 1rem;
  overflow-x: clip;
  background: var(--cardColor);
}
.profileImages {
  position: relative;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}

.profileImages > img:nth-of-type(1) {
  width: 100%;
}

.profileImages > img:nth-of-type(2) {
  width: 6rem;
  height: 6rem;
  object-fit: cover;
  border-radius: 50%;
  position: absolute;
  bottom: -3rem;
  box-shadow: var(--profileShadow);
}

.profileName {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 3rem;
  gap: 10px;
}

.profileName > span:nth-of-type(1) {
  font-weight: bold;
}

/* FOllow Status */
.followStatus {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 0.75rem;
}

hr {
  width: 85%;
  border: 0.5px solid var(--hrColor);
  margin: 10px 0;
}

.followStatus > div {
  display: flex;
  gap: 1rem;
  width: 80%;
  justify-content: space-around;
  align-items: center;
}

.follow {
  display: flex;
  flex-direction: column;
  gap: 0.4rem;
  align-items: center;
  justify-content: center;
}

.follow > span:nth-of-type(1) {
  font-weight: bold;
}

.follow > span:nth-of-type(2) {
  color: var(--gray);
}

.vl {
  height: 150%;
  border-left: 2px solid var(--hrColor);
}

.profileCard > span {
  font-weight: bold;
  color: blue;
  align-self: center;
  margin-bottom: 1rem;
  cursor: pointer;
}
